import * as React from 'react';
import { connect } from 'react-redux';
import { Classes } from '@blueprintjs/core';
import { listOfThemes } from 'utils';
import { editStyle } from 'actions';
export const ThemeSelectBase = ({ theme, onChange }) => React.createElement("div", { className: Classes.SELECT },
    React.createElement("select", { value: theme, onChange: e => onChange({ template: e.target.value }) }, listOfThemes.map(o => React.createElement("option", { value: o }, o))));
export const ThemeSelect = connect(null, { onChange: editStyle })(ThemeSelectBase);
