import * as React from 'react';
import './Autocomplete.styl';
export class Autocomplete extends React.Component {
    constructor(props) {
        super(props);
        this.updateItems = (e) => {
            this.setState({
                currentValue: e.target.value,
                visibleItems: this.props.items.filter(i => i.startsWith(e.target.value)),
            });
            this.props.onChange(e);
        };
        this.openList = e => this.setState({ isOpen: true });
        this.closeList = e => {
            this.setState({ isOpen: false });
            this.setState({ visibleItems: [] });
        };
        this.handleKeyDown = e => {
            if (e.which === 13) {
                if (this.state.visibleItems.includes(this.state.currentValue)) {
                    this.closeList(e);
                }
                else {
                    this.selectItem(this.state.visibleItems[0]);
                }
            }
            if (e.which === 27 || e.which === 13 || e.which === 9) {
                this.closeList(e);
            }
            if (e.which === 38 || e.which === 40) {
                this.handleMovement(e);
                this.setState({ isOpen: true });
            }
        };
        this.handleMovement = e => {
            const currentIndex = this.state.visibleItems.indexOf(this.state.currentValue);
            if (e.which === 38) {
                this.selectItem(this.state.visibleItems[currentIndex - 1]);
            }
            else {
                this.selectItem(this.state.visibleItems[currentIndex + 1]);
            }
        };
        this.state = {
            visibleItems: [],
            currentValue: '',
            isOpen: false,
        };
    }
    selectItem(v) {
        console.log(v);
        this.setState({ currentValue: v, isOpen: false });
        this.props.onChange({
            target: {
                value: v,
            },
        });
    }
    componentWillMount() {
        this.setState({ currentValue: this.props.value });
    }
    componentWillUnmount() {
        this.setState({
            isOpen: false,
            visibleItems: [],
        });
    }
    componentWillReceiveProps(nextProps) {
        this.setState({ currentValue: nextProps.value });
    }
    renderItems() {
        return this.state.visibleItems.map((v, i) => {
            return (React.createElement("li", { key: i, role: 'item', onClick: e => this.selectItem(v), style: v === this.state.currentValue ? { color: 'lightblue' } : {} }, v));
        });
    }
    render() {
        return (React.createElement("div", { className: 'current-pokemon-input-wrapper autocomplete' },
            React.createElement("label", null, this.props.label),
            React.createElement("input", { onKeyDown: this.handleKeyDown, onFocus: this.openList, onBlur: this.closeList, placeholder: this.props.placeholder, name: this.props.name, type: 'text', onChange: this.updateItems, value: this.state.currentValue }),
            this.state.isOpen ? (React.createElement("ul", { className: 'autocomplete-items has-nice-scrollbars' }, this.renderItems())) : null));
    }
}
